import "./styles.css";
import React, { useState } from "react";

var welcomeMsg = "Emoji Interpreter";
const emojiDictionary = {
  "ðŸ˜€": "Happy or Smiling",
  "ðŸ˜‰": "Winking",
  "ðŸ¤©": "Star struck or super excited",
  "ðŸ˜¬": "Grimacing",
  "ðŸ˜“": "Sad sweat face",
  "ðŸ˜³": "Blushing"
};

export default function App() {
  /* const [likeCounter, setLikeCounter] = useState(0);**/
  var [userInput, setUserInput] = useState("");
  var myEmojiDb = Object.keys(emojiDictionary); /** object to array comversion*/

  function inputChangeHandler(event) {
    var inputEmoji = event.target.value;
    if (emojiDictionary[inputEmoji] === undefined) {
      setUserInput(" This emoji does not exist in our current dictionary.");
    } else {
      setUserInput(emojiDictionary[inputEmoji]);
    }
  }
  function clickHandler(emoji) {
    setUserInput(emojiDictionary[emoji]);
  }
  return (
    <div className="App">
      <div class="container">
        <h1>{welcomeMsg}</h1>
      </div>

      <input
        placeholder="paste your emoji here"
        onChange={inputChangeHandler}
      ></input>

      <div id="outputDiv">
        <p>{userInput}</p>
      </div>
      <div id="spanEmoji">
        <h2> The emojis in our database are:</h2>
        {myEmojiDb.map((emoji) => {
          return (
            <span
              onClick={() => clickHandler(emoji)}
              style={{ fontSize: "2rem", padding: "0.5rem", cursor: "pointer" }}
              id="emojiSpan"
            >
              {emoji}
            </span>
          );
        })}
      </div>
    </div>
  );
}
